@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

.navb{
    display: flex;
    flex-direction: row;
    height: 60px;
    width: 100%;
    background-color: #000000;
}

img{
    height: 50px;
    width: 100px;
    object-fit: contain;
}

.container{
    display: flex;
    flex-direction: row;
    gap: 50px;
    letter-spacing: 1px;
    margin-left: auto;
    margin-right: 30px;
    color: whitesmoke;
    font-family: 'Lato', sans-serif;
}
